<template>
  <div class="love-oath">
    <div class="banner">
      <div id="scene">
        <div data-depth="0.00" class="layer"></div>
        <div data-depth="0.88" class="stars-cluster stars-cluster-1 layer">
          <img src="../../static/img/banner_icon03.png" class="banner-icon03">
        </div>
        <div data-depth="0.99" class="moon-wrapper layer">
          <img src="../../static/img/banner_icon02.png" class="banner-icon02">
        </div>
        <div data-depth="0.66" class="title-wrapper layer">
          <img src="../../static/img/banner_icon01.png" class="banner-icon01">
        </div>
        <div data-depth="0.77" class="title-wrapper layer">
          <div class="btn">
            <router-link to="/index"  class="btn-left"></router-link>
            <router-link to="/myLove"  class="btn-right"></router-link>
          </div>
        </div>
      </div>
    </div>
    <!--/banner-->

    <div class="row my-oath issue-an-oath">
      <h2></h2>
      <form @submit="onSubmit" @reset="onReset">
        <div class="thumbnail">
          <div class="con4">
            <canvas id="cvs" width="220" height="220"></canvas>
            <span class="btn upload"><input type="file" class="upload_pic" id="upload" v-on:change="getFile"></span>
            <div class="prompt">300*300</div>
          </div>
        </div>
        <div class="caption">
          <div class="form-group">
            <label for="exampleInputEmail1">*</label>
            <input type="text" class="form-control" v-model="form.title">
          </div>
          <div class="form-group">
            <label for="exampleInputEmail1">*</label>
            <textarea class="form-control" rows="3" v-model="form.message"></textarea>
          </div>
          <div class="form-group">
            <button type="reset" class="btn"></button>
            <button type="submit" class="btn btn-default"></button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script >
import api from '../libs/api'
import image from '../libs/image'

export default {
  name: 'CreateLove',
  data () {
    return {
      form: {
        title: '',
        message: '',
        file: ''
      }
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      image.PhotoCompress(this.form.file, {
        quality: 0.8,
        width: 320,
      }, re => {
        api.publishLove(this.form.title, this.form.message, re, resp => {
        })
      })
    },
    onReset (evt) {
      evt.preventDefault()
      this.form.title = ''
      this.form.message = ''
      this.form.file = ''
    },
    getFile($event){
      this.form.file = $event.target.files[0] //
      if(!/image\/\w+/.test(this.form.file.type)){
        //typeimage/pngimage/jpg
        alert("");
        return false;
      }
      // console.log(file);
      var reader = new FileReader();//
      reader.readAsDataURL(this.form.file);//url
      //
      reader.onload = function(e){

        var image = new Image();
        // src
        image.src = e.target.result;
        var max=200;
        // load
        image.onload = function(){
          //  canvas DOM 
          var canvas = document.getElementById("cvs");
          //   *=
          /*if(image.height > max) {
                    image.width *= max / image.height;
                    image.height = max;
                } */
          //  canvas 2d ,
          var ctx = canvas.getContext("2d");
          // canvas
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          // canvas
          /*canvas.width = image.width;
                canvas.height = image.height;
                if (canvas.width>max) {canvas.width = max;}*/
          // canvas
          // ctx.drawImage(image, 0, 0, image.width, image.height);
          ctx.drawImage(image, 0, 0, 220, 220);
          // imagedom
        };
      }
    }
  }
}
</script>



